<template>
  <div id="header">
  <el-select v-model="time" placeholder="请选择" size="mini">
    <el-option
      v-for="item in timeselect"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  <el-select v-model="category" placeholder="请选择" size="mini" class="category">
    <el-option
      v-for="item in menu"
      :key="item.id"
      :label="item.name"
      :value="item.id">
    </el-option>
  </el-select>
  </div>
</template>

<script>
  import menu from '@/config/menu-config'
  export default {
    data() {
      return {
        timeselect: [{
          value: 'option1',
          label: 'Since Last Vist'
        }, {
          value: 'option2',
          label: 'All Time'
        }],
        time: 'option1',
        menu: menu,
        category: '1'
      }
    }
  }
</script>

<style>
  #header {
    margin-top: 60px;
  }
  .el-input__inner {
    font-weight: bold;
    font-size: 13px;
        background-color: #DEDEDE;
  }
  .el-select .el-input .el-select__caret {
    color: #2c3e50;
  }
  .el-select {
    width: 160px;
  }
  .category {
    margin-left: 30px;
  }
</style>


